import { Html, useGLTF, useAnimations } from "@react-three/drei"; //Camera Controls
import React, { useState, useContext, useRef } from "react";
import { StoredData } from "../Clouds/Context";

const LinkClickableButton = ({buttonName = "Add Name", buttonPosition = [-10,0,-5], closeButtonPosition = [8, 12, -12], rotation = [0,-1.64,0], scale = [-6,6,6], imgSrc = "", redirName = ""})=>
{
    const {data, updateDropPhysics, updateRenderingSpace} = useContext(StoredData);
    return (
        <>
            <mesh position={buttonPosition} onClick={function(){updateRenderingSpace(redirName)}}>
                <Html>
                    <div onClick={function(){
                        updateRenderingSpace(redirName);
                    }} style={{zIndex: 2}}>
                        <h1 className='worldSpaceClickableButton'>{buttonName}</h1>
                    </div> 
                </Html>
                {/* <Model /> */}
            </mesh> 
        </>
    );
}

export default LinkClickableButton;


function Model(props) {
    const group = useRef()
    const { nodes, materials, animations } = useGLTF('/glb/Homie_Optimized.glb')
    const { actions } = useAnimations(animations, group)
    return (
      <group ref={group} {...props} dispose={null} scale={3}>
        <group name="Scene">
          <mesh
            name="connector"
            castShadow
            receiveShadow
            geometry={nodes.connector.geometry}
            material={materials.base}
            position={[0, 0.202, -0.015]}
          />
        </group>
      </group>
    )
}
  
useGLTF.preload('/glb/Homie_Optimized.glb')